.bg-white {
  background-color: white;
}
.min-h-100 {
  min-height: 100vh;
}

/* user-profile-photo */
.user-upload {
  position: relative;
  max-width: 205px;
  margin: 10px auto;
}
.user-upload .user-edit {
  position: absolute;
  right: 12px;
  z-index: 1;
  top: 10px;
}
.user-upload .user-edit input {
  display: none;
}
.user-upload .user-edit input + label {
  display: inline-block;
  width: 34px;
  height: 34px;
  margin-bottom: 0;
  border-radius: 100%;
  background: #ffffff;
  border: 1px solid transparent;
  box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, 0.12);
  cursor: pointer;
  font-weight: normal;
  transition: all 0.2s ease-in-out;
}
.user-upload .user-edit input + label:hover {
  background: #f1f1f1;
  border-color: #d6d6d6;
}
